a,button{
  transition: all 0.3s;
}
.footer { 
  width: 100%;
  background: #373842;
  border-radius: 160px 160px 0 0px;
  padding: 100px 20px;
	.footer_cont { 
    max-width: 1170px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
		.footer_left { 
      max-width: 504px;
      width: 100%;
			.logo { 
        width: 100%;
        display: block;
        max-width: 127px;
        margin-bottom: 25px;
				img { 
          width: 100%;
				}
			}

			h2 { 
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 300;
        font-size: 40px;
        margin: 0;
        line-height: 152%;
        color: #FFFFFF;
			}
		}

		.footer_right { 
      width: 100%;
      max-width: 538px;
			ul { 
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 0;
        padding: 0;
        margin-bottom: 40px;
				li { 
          list-style: none;
					a { 
            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 17px;
            color: #FFFFFF;
            mix-blend-mode: normal;
            opacity: 0.8;
					}
				}
			}

			.soc { 
        width: 100%;
        display: flex;
        justify-content: flex-end;
        align-items: center;
				a { 
          display: flex;
          justify-content: center;
          align-items: center;
          margin-left: 20px;
          width: 32px;
          height: 32px;
          background: rgba(243, 243, 243,0.1);
          border-radius: 7px;
					svg { 

						rect { 

						}

						g { 

							path { 

							}
						}

						defs { 

							#clip0_329_6093 { 

								rect { 

								}
							}
						}
					}
				}
			}
		}
	}
}
@media(max-width: 1170px){
  .footer { 
    border-radius: 80px 80px 0 0px;
    padding: 100px 20px;
    .footer_cont { 
      .footer_left { 
        max-width: 504px;
        .logo { 
          max-width: 127px;
          margin-bottom: 25px;
        }
        h2 {
          font-size: 30px;
        }
      }
      .footer_right { 
        max-width: 538px;
        ul { 
          display: none;
        }
        .soc { 
          a { 
            margin-left: 20px;
            width: 32px;
            height: 32px;
          }
        }
      }
    }
  }
}
@media(max-width: 800px){
  .footer { 
    border-radius: 30px 30px 0 0px;
    padding: 50px 20px;
    .footer_cont { 
      flex-direction: column;
      align-items: center;
      .footer_left { 
        max-width: unset;
        text-align: center;
        margin-bottom: 30px;
        .logo { 
          margin: 0 auto;
          max-width: 127px;
          margin-bottom: 25px;
        }
        h2 {
          font-size: 24px;
          text-align: center;
        }
      }
      .footer_right { 
        max-width: 538px;
        ul { 
          margin-bottom: 40px;
          flex-wrap: wrap;
          gap: 10px;
          justify-content: center;
          li { 
            a { 
              font-size: 14px;
              line-height: 17px;
            }
          }
        }
        .soc { 
          justify-content: center;
          gap: 10px;
          a { 
            margin-left: 0px;
            width: 32px;
            height: 32px;
          }
        }
      }
    }
  }
}