.Roadmap {
  width: 100%;
  overflow: hidden;
  padding: 55px 20px 174px;
  position: relative;
	.Roadmap_cont {
    width: 100%;
    max-width: 1180px;
    margin: 0 auto;
		h2 {
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 300;
      font-size: 42px;
      line-height: 130%;
      color: #F3F3F3;
      margin-bottom: 51px;
		}
		.Roadmap_swiper {
      width: 100%;
      overflow: unset;
			.swiper-wrapper {
        .swiper-slide2{
          max-width: 293px;
          .Roadmap_item .item_top .Roadmap_info{
            border: unset!important;
            &::before{
              opacity: 0;
            }
          }
        }
				.swiper-slide {
          width: 100%;
          min-width: max-content;
					.Roadmap_item {
            width: 100%;
						.item_top {
              width: 100%;
              display: flex;
              justify-content: space-between;
              align-items: stretch;
							.Roadmap_info {
                width: 100%;
                max-width: 293px;
                padding-left: 20px;
                padding-right: 20px;
                min-height: 348px;
                border-left: 1px solid #F3F3F3;
                position: relative;
                &::before{
                  content: "";
                  width: 11px;
                  height: 11px;
                  display: block;
                  border-radius: 50%;
                  position: absolute;
                  left: -6px;
                  top: 0;
                  background: #F3F3F3;
                }
								h3 {
                  font-family: 'Montserrat';
                  font-style: normal;
                  font-weight: 700;
                  font-size: 28px;
                  line-height: 1;
                  margin-bottom: 20px;
                  color: #F3F3F3;
								}
								p {
                  position: relative;
                  display: flex;
                  justify-content: flex-start;
                  align-items: center;
                  font-family: 'Montserrat';
                  font-style: normal;
                  font-weight: 400;
                  font-size: 13px;
                  line-height: 155%;
                  color: #F3F3F3;
                  margin-bottom: 10px;
                  &::before{
                    content: url("../img/pstar.svg");
                    position: relative;
                    margin-right: 12px;
                    min-width: 18px;
                  }

                  &.done {
                    &::before{
                      content: url("../img/pstar-green.svg");
                      position: relative;
                      margin-right: 12px;
                      min-width: 18px;
                    }
                  }
								}

								.spans {
                  width: 95%;
                  position: absolute;
                  display: flex;
                  justify-content: space-between;
                  align-items: center;
                  bottom: -22px;
                  left: -10px;
									span {
                    width: 17px;
                    height: 17px;
                    display: block;
                    border-radius: 50%;
                    border: 2px solid #F3F3F3;
                    &:first-child{
                      background: #F3F3F3;
                      border: 2px solid #F3F3F3;
                    }
									}
								}
							}
              .green{
                border-left: 1px solid #DDF247;
                &::before{
                  background: #DDF247;
                }
                h3{
                  color: #DDF247;
                }
                .spans{
                  span{
                    border: 2px solid #DDF247;
                    &:first-child{
                      background: #DDF247;
                      border: 2px solid #DDF247;
                    }
                  }
                }
              }
						}
						.item_bottom {
              width: 100%;
              display: flex;
              justify-content: space-between;
              align-items: flex-start;
              margin-top: 44px;
              gap: 20px;
							.bottom_info {
                max-width: 433px;
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: stretch;
                flex-wrap: wrap;
                gap: 20px 6px;
                &:first-child{
                  p{
                    font-family: 'Montserrat';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 12px;
                    line-height: 142%;
                    text-align: center;
                    color: #F3F3F3;
                    background: #373842;
                    padding: 10px 8px;
                    &:first-child{
                      font-family: 'Montserrat';
                      font-style: normal;
                      font-weight: 700;
                      font-size: 14px;
                      line-height: 155%;
                      padding: 12px;
                      color: #F3F3F3;
                    }
                  }
                }
                &:nth-child(2){
                  max-width: 283px;
                  p{
                    &:last-child{
                      min-height: 47px;
                      display: flex;
                      justify-content: center;
                      align-items: center;
                    }
                  }
                }
                &:nth-child(3){
                  max-width: 511px;
                  flex-wrap: nowrap;
                  gap: 20px;
                  p{
                    position: relative;
                    &::before{
                      content: "";
                      position: absolute;
                      left: 50%;
                      transform: translateX(-50%);
                      bottom: -16px;
                      border: 8px solid transparent;
                      border-top: 8px solid #484956;
                    }
                  }
                }
								p {
                  background: #484956;
                  border-radius: 8px;
                  margin: 0;
                  padding: 12px;
                  font-family: 'Montserrat';
                  font-style: normal;
                  font-weight: 400;
                  font-size: 12px;
                  line-height: 142%;
                  text-align: center;
                  color: #F3F3F3;
                  &:first-child{
                    font-family: 'Montserrat';
                    font-style: normal;
                    font-weight: 700;
                    font-size: 14px;
                    line-height: 155%;
                    color: #F3F3F3;
                    width: 100%;
                    position: relative;
                    &::before{
                      content: "";
                      position: absolute;
                      left: 50%;
                      transform: translateX(-50%);
                      bottom: -16px;
                      border: 8px solid transparent;
                      border-top: 8px solid #484956;
                    }
                  }
								}
							}
						}
					}
				}
			}
		}
	}
}
@media(max-width: 960px){
  .Roadmap .Roadmap_cont h2{
    font-size: 34px;
  }
}
@media(max-width: 768px){
  .Roadmap .Roadmap_cont h2{
    margin-bottom: 30px;
    font-size: 28px;
  }
  .Roadmap {
    padding: 40px 20px ;
    .Roadmap_cont {
      .Roadmap_swiper {
        .swiper-wrapper {
          .swiper-slide2{
            max-width: 293px;
          }
          .swiper-slide {
            .Roadmap_item {
              .item_top {
                .Roadmap_info {
                  max-width: 293px;
                  padding-left: 20px;
                  min-height: 280px;
                  &::before{
                    width: 10px;
                    height: 10px;
                    left: -6px;
                  }
                  h3 {
                    font-size: 24px;
                    line-height: 30px;
                    margin-bottom: 20px;
                  }
                  p {
                    font-size: 14px;
                    margin-bottom: 10px;
                    &::before{
                      margin-right: 12px;
                      min-width: 18px;
                    }
                  }
                  .spans {
                    width: 95%;
                    bottom: -10px;
                    left: -5px;
                    span {
                      width: 10px;
                      height: 10px;
                    }
                  }
                }
              }
              .item_bottom {
                margin-top: 44px;
                gap: 20px;
                .bottom_info {
                  max-width: 280px;
                  gap: 10;
                  p {
                    width: 100%!important;
                    padding: 8px;
                    font-size: 12px;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
