.main {
  padding: 133px 16px 24px;
  position: relative;
	.main_cont {
    max-width: 1180px;
    width: 100%;
    margin: 0 auto;
		.main_text {
      max-width: 572px;
      width: 100%;
			.audit {
        max-width: 234px;
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        background: #373842;
        border-radius: 15px;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        padding-left: 15px;
        font-size: 14px;
        line-height: 22px;
        height: 54px;
        color: #FFFFFF;
        opacity: 0.3;
				span {
          height: 100%;
          font-family: 'Montserrat';
          font-style: normal;
          font-weight: 700;
          font-size: 10px;
          width: 100%;
          max-width: 117px;
          line-height: 18px;
          color: #101010;
          display: flex;
          justify-content: center;
          align-items: center;
          background: #DDF247;
          border-radius: 15px;
					svg {
            margin-right: 10px;
						path {

						}
					}
				}
			}

			h1 {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 300;
        font-size: 52px;
        line-height: 130%;
        color: #F3F3F3;
        margin: 30px 0 35px;
			}
			p {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 155%;
        color: #F3F3F3;
			}
			.btn_flex {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        max-width: 347px;
        margin-top: 65px;
				a {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 100%;
          height: 50px;
          font-family: 'Montserrat';
          font-style: normal;
          font-weight: 600;
          font-size: 16px;
          line-height: 26px;
          text-align: center;
          &:hover{
            box-shadow: 0 0 15px #FFF;
          }
          &:first-child{
            color: #101010;
            max-width: 139px;
            background: #F3F3F3;
            border-radius: 8px;
          }
          // &:last-child{
          //   border: 1px solid #F3F3F3;
          //   box-sizing: border-box;
          //   border-radius: 8px;
          //   color: #F3F3F3;
          //   max-width: 163px;
          // }
				}
			}
		}

		.img {
      position: absolute;
      width: 658px;
      height: 786px;
      background-image: url("../img/serachimgbgg.png");
      top: -97px;
      right: 0;
      overflow: hidden;
      .img_sizehide{
        position: absolute;
        right: 0;
        bottom: 0;
        width: 693px;
        height: 670px;
        overflow: hidden;
        clip-path: polygon(65% 0, 100% 0, 100% 43%, 100% 95%, 71% 96%, 36% 91%, 15% 76%, 10% 53%, 24% 20%);
        .imgbox {
          position: absolute;
          left: 127px;
          width: 441px;
          height: 497px;
          bottom: -16px;
          .img2{
            position: absolute;
            right: 17px;
            width: 154px;
            top: 16px;
            height: 155px;
            background-size: cover;
            background-position: center;
            transition: all 0.5s;
            background-image: url("../img/imgloop.png");
            border-radius: 50%;
          }
          img:nth-child(2){
            max-width: 441px;
            width: 100%;
            height: 403px;
          }
        }
      }

		}
	}
}
@media(max-width: 1230px){
  .main {
    padding: 80px 16px 20px;
    .main_cont {
      .main_text {
        max-width: 450px;
        position: relative;
        z-index: 2;
        .audit {
          max-width: 234px;
          padding-left: 15px;
          font-size: 14px;
          line-height: 22px;
          height: 54px;
          span {
            font-size: 10px;
            max-width: 117px;
            line-height: 18px;
            border-radius: 15px;
            svg {
              margin-right: 10px;
            }
          }
        }
        h1 {
          font-size: 40px;
          margin: 30px 0 35px;
        }
        p {
          font-size: 16px;
        }
        .btn_flex {
          max-width: 347px;
          margin-top: 65px;
          a {
            height: 50px;
            font-size: 16px;
            line-height: 26px;
            &:first-child{
              max-width: 139px;
            }
            &:last-child{
              max-width: 163px;
            }
          }
        }
      }
      .img{
        width: 470px;
        height: 700px;
      }
      .img {
        .img_sizehide {
          right: -6px;
          bottom: -41px;
          width: 436px;
          height: 491px;
          .imgbox {
            position: absolute;
            left: 28px;
            width: 314px;
            height: 497px;
            bottom: -169px;
            img:nth-child(2) {
              height: 296px;
            }
            .img2{
              right: 10px;
              width: 112px;
              top: 11px;
              height: 112px;
            }
          }
        }
      }
    }
  }

}
@media(max-width: 820px){
  .main {
    padding: 20px 16px 0px;
    position: relative;
    z-index: 23;
    .main_cont {
      .main_text {
        text-align: center;
        margin: 0 auto;
        .audit{
          margin: 0 auto 30px;
        }
        h1 {
          font-size: 26px;
          margin: 0px 0 20px;
        }
        p {
          font-size: 14px;
        }
        .btn_flex {
          max-width: 347px;
          margin: 0 auto;
          margin-top: 30px;
          flex-direction: column;
          a {
            height: 50px;
            font-size: 16px;
            line-height: 26px;
            &:first-child{
              max-width: unset;
              margin-bottom: 20px;
            }
            &:last-child{
              max-width: unset;
            }
          }
        }
      }
      .img {
        width: 300px;
        height: 300px;
        background-size: 100%;
        top: unset;
        display: none;
        bottom: 40px;
        .img_sizehide {
          right: -6px;
          bottom: -41px;
          width: 501px;
          height: 491px;
        }
        .imgbox{
          left: 95px;
          width: 200px;
          height: 200px;
          bottom: -80px;
          .img2{
            right: 27px;
            top: 13px;
            width: 84px;
          }
        }
      }
    }
  }
}
