.cirkularimg_js{
  transition: all 0.3s;
}
.solutions { 
  width: 100%;
  padding: 56px 20px 174px;
  position: relative;
  background-image: url("../img/gradientbg5.png");
  background-repeat: no-repeat;
  background-position: left -84px;
  .bottombg{
    background-image: url("../img/gradientbg6.png");
    background-repeat: no-repeat;
    background-position: right bottom;
    background-size: contain;
    width: 839px;
    height: 839px;
    right: -84px;
    bottom: -143px;
    position: absolute;
    z-index: -1;
  }
  .bigspiral{
    position: absolute;
    bottom: 0;
    right: -318px;
    transition: all 0.3s;
    
  }
	.solutions_cont { 
    width: 100%;
    max-width: 1180px;
    margin: 0 auto;
		.solutions_item { 
      width: 100%;
      position: relative;

      &:nth-child(2){
        margin-bottom: 230px;
        position: relative;
      }
      &:nth-child(3){
        position: relative;
        
      }
			h2 { 
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 300;
        font-size: 42px;
        line-height: 130%;
        color: #F3F3F3;
        margin-bottom: 70px;
			}

			.swiper { 
        width: 100%;
        overflow: unset;
				.swiper-wrapper { 
          display: flex;
          justify-content: flex-start;
          align-items: stretch;
					.swiper-slide { 
            width: 100%;
            height: auto;
            height: 255px;
            max-width: 282px;
            padding: 78px 33px 0px;
            background: rgba(55, 56, 66, 0.5);
            box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.15);
            backdrop-filter: blur(15px);
            border-radius: 20px;
            position: relative;
            .type { 
              width: 100%;
              max-width: 255px;
              position: absolute;
              padding: 20px;
              top: 0;
              left: 0;
              display: flex;
              justify-content: flex-end;
              align-items: center;
              img { 

              }
            }

            p { 
              font-family: 'Montserrat';
              font-style: normal;
              font-weight: 400;
              font-size: 16px;
              line-height: 20px;
              text-align: center;
              color: #F3F3F3;
              max-width: 216px;
              margin: 0 auto;
            }
					}
				}
			}
		}
	}
}
@media(max-width: 1024px){
  .solutions {
    padding: 56px 20px 74px;
  }
  .solutions .solutions_cont .solutions_item:nth-child(2){
    margin-bottom: 90px;
  }
  .solutions .solutions_cont .solutions_item h2{
    font-size: 34px;
  }
}
@media(max-width: 780px){
  .solutions .solutions_cont .solutions_item .swiper .swiper-wrapper .swiper-slide .type{
    padding: 10px;
    img{
      width: 20px;
    }
  }
  .solutions {
    padding: 0px 20px 0px;
    background-image: unset;
    .bottombg{ 
      display: none!important;
    }
    .bigspiral{
      right: -200px;
    }
  }
  .solutions .solutions_cont .solutions_item{
    &::before{
      display: none;
    }
  }
  .solutions .solutions_cont .solutions_item .swiper .swiper-wrapper .swiper-slide {
    max-width: 235px;
    padding: 40px 16px 30px;
    height: auto;
    p{
      font-size: 12px;
    }
  }
  .solutions .solutions_cont .solutions_item h2{
    font-size: 26px;
    margin-bottom: 30px;
  }
  .solutions .solutions_cont .solutions_item:nth-child(2){
    margin-bottom: 40px;
  }
}