.works { 
  width: 100%;
  padding: 55px 20px 91px;
	.works_cont { 
    width: 100%;
    max-width: 1180px;
    margin: 0 auto;
		h2 { 
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 300;
      font-size: 42px;
      line-height: 130%;
      margin-bottom: 77px;
      color: #F3F3F3;
		}
    img{
      width: 100%;
    }
		.works_grid { 
      width: 100%;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
			.works_item { 
        width: auto;
				img { 
          width: 100%;
          height: 405px;
          object-fit: scale-down;
          display: block;
          object-position: left;
          margin-bottom: 27px;
				}
				h3 { 
          max-width: 343px;
          width: 100%;
          margin-bottom: 11px;
          font-family: 'Montserrat';
          font-style: normal;
          font-weight: 700;
          font-size: 28px;
          line-height: 34px;
          color: #F3F3F3;
				}

				p { 
          max-width: 343px;
          font-family: 'Montserrat';
          font-style: normal;
          font-weight: 400;
          font-size: 14px;
          line-height: 132%;
          color: #F3F3F3;          
				}
			}
		}

		.lasttext { 
      width: 100%;
      max-width: 698px;
      margin: 70px auto 0;
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 26px;
      text-align: center;
      color: #F3F3F3;      
		}
	}
}
@media(max-width: 960px){
  .works { 
    padding: 55px 20px 60px;
    .works_cont { 
      h2 { 
        font-size: 34px;
        margin-bottom: 50px;
      }
      .works_grid { 
        grid-template-columns: repeat(3, 1fr);
        .works_item {
          img { 
            height: 260px;
            margin-bottom: 27px;
          }
          h3 { 
            max-width: 200px;
            margin-bottom: 11px;
            font-size: 20px;
            line-height: 26px;
          }
          p { 
            max-width: 180px;
            font-size: 12px;  
          }
        }
      }
      .lasttext { 
        max-width: 698px;
        margin: 70px auto 0;
        font-size: 14px;
        line-height: 26px;
      }
    }
  }
}
@media(max-width: 780px){
  .works { 
    padding: 40px 20px 0px;
    .works_cont { 
      h2 { 
        font-size: 26px;
        margin-bottom: 40px;
      }
      .works_grid { 
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        flex-wrap: wrap;
        gap: 20px 10px;
        .works_item {
          width: 48%;
          &:last-child{
            width: 100%;
          }
          img { 
            max-width: 300px;
            height: auto;
            margin-bottom: 27px;
          }
          h3 { 
            max-width: unset;
            margin-bottom: 11px;
            font-size: 16px;
            line-height: 20px;
          }
          p { 
            max-width: unset;
            font-size: 12px;  
          }
        }
      }
      .lasttext { 
        max-width: 698px;
        margin: 40px auto 0;
        font-size: 14px;
        line-height: 26px;
      }
    }
  }
}