.team { 
  width: 100%;
  padding: 55px 20px 230px;
  padding-left: 25px;
	.team_cont { 
    width: 100%;
    max-width: 1180px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
		.team_thumbs { 
      width: 100%;
      max-width: 543px;
      margin-right: 10px;
			.swiper { 
        width: 100%;
        background-image: url("../img/teambg.svg");
        height: 513px;
        background-repeat: no-repeat;
        background-position: bottom;
				.swiper-wrapper { 
          width: 100%!important;
          transform: unset!important;
					.swiper-slide { 
            padding: 10px;
            border-radius: 50%;
            height: max-content!important;
            width: max-content!important;
            border: 1px solid #DDF247;
            cursor: pointer;
            position: absolute;
            &:nth-child(1){
              left: 22%;
              top: 62px;
              max-width: 61px;
              padding: 5px;
            }
            &:nth-child(2){
              right: 0%;
              top: 54%;
              max-width: 76px;
            }
            &:nth-child(3){
              left: 31%;
              bottom: 0;
              max-width: 67px;
              padding: 8px;
            }
            &:nth-child(4){
              top: 40%;
              left: 37%;
              max-width: 46px;
              padding: 6px;
            }
            &:nth-child(5){
              top: 45%;
              left: 0%;
              max-width: 67px;
              padding: 8px;
            }
            &:nth-child(7){
              left: 30%;
              max-width: 84px;
            }
            &:nth-child(6){
              right: 8%;
              top: 18%;
              
              max-width: 56px;
              padding: 8px;
            }
						img { 
              border-radius: 50%;
              width: 100%;
						}
					}
				}
			}
		}

		.team_right { 
      width: 100%;
      max-width: 588px;
			h2 { 
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 300;
        font-size: 42px;
        line-height: 130%;
        color: #F3F3F3;        
        margin-bottom: 70px;
			}
			.flex { 
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
				.btn { 
          display: flex;
          justify-content: flex-start;
          align-items: flex-start;
          flex-direction: column-reverse;
          max-width: 42px;
          button{
            width: 42px;
            height: 42px;
            background: #101010;
            box-shadow: 0px 6px 10px rgba(243, 243, 243, 0.06);
            border-radius: 50%;
            transition: all 0.3s;
            cursor: pointer;
            padding: 0;
            display: flex;
            justify-content: center;
            align-items: center;
            svg path{
              transition: all 0.3s;
            }
            &:first-child{
              margin-top: 25px;
            }
            &:hover{
              background: #F3F3F3;
              filter: unset;
              svg path{
                stroke: #101010!important;
              }
            }
          }
				}

				.teamSwiper { 
          width: 100%;
          max-width: 486px;
					.swiper-wrapper {   
            width: 100%;
            display: flex;
						.swiper-slide { 
              width: 100%;
              opacity: 0!important;
							.team_item { 
                padding: 45px 38px;
                background: #373842;
                border-radius: 30px;
                width: 100%;
								.item_title { 
                  width: 100%;
                  display: flex;
                  justify-content: space-between;
                  align-items: center;
                  position: relative;
                  margin-bottom: 30px;
                  .alllink{
                    width: 100%;
                    height: 100%;
                    z-index: 2;
                    position: absolute;
                    display: block;
                    left: 0;
                    top: 0;
                    
                  }
									img { 
                    width: 100px;
                    height: 100px;
                    border-radius: 50%;
                    object-fit: cover;
									}
                  .item_img{
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    max-width: 148px;
                    width: 100%;
                  }
									.titletext { 
                    max-width: 244px;
                    width: 100%;
                    position: relative;
										h3 { 
                      font-family: 'Montserrat';
                      font-style: normal;
                      font-weight: 700;
                      font-size: 22px;
                      line-height: 29px;
                      color: #DDF247;
                      margin-bottom: 10px;
										}
										p { 
                      font-family: 'Montserrat';
                      font-style: normal;
                      font-weight: 400;
                      font-size: 16px;
                      line-height: 22px;
                      color: #F3F3F3;
										}
									}
								}

								.infotext { 
                  font-family: 'Montserrat';
                  font-style: normal;
                  font-weight: 400;
                  font-size: 16px;
                  line-height: 155%;
                  color: #F3F3F3;
								}
							}
						}
            .swiper-slide-active{
              opacity: 1!important;
            }
					}
				}
			}
		}
	}
}
@media(max-width: 1140px){
  .team { 
    padding: 55px 20px 100px;
    .team_cont { 
      flex-direction: column;
      align-items: flex-start;
      .team_thumbs { 
        display: none;
      }
  
      .team_right { 
        max-width: unset;
        .cub{
          width: 62px;
          gap: 4px;
          left: 0px;
          top: -20px;
        }
        h2 { 
          font-size: 34px;     
          margin-bottom: 20px;
        }
        .flex { 
          flex-direction: column;
          max-width: unset;
          justify-content: flex-start;
          align-items: flex-start;
          .btn { 
            max-width: unset;
            flex-direction: row;
            margin-bottom: 20px;
            button{
              &:first-child{
                margin-top: 0px;
                margin-right: 10px;
              }
            }
          }
  
          .teamSwiper { 
            max-width: unset;
            .swiper-wrapper {   
              .swiper-slide { 
                opacity: 0!important;
                transition: all 0.3s;
                .team_item { 
                  height: auto;
                  .item_title { 
                    margin-bottom: 30px;
                    justify-content: flex-start;
                    gap: 16px;
                    img { 
                      width: 100px;
                      height: 100px;
                    }
                    .titletext { 
                      max-width: 320px;
                      &::before{
                        left: -52px;
                      }
                      h3 { 
                        font-size: 24px;
                        line-height: 29px;
                      }
                      p { 
                        font-size: 18px;
                        line-height: 22px;
                      }
                    }
                  }
                  .infotext { 
                    font-size: 16px;
                    max-width: unset;
                  }
                }
              }
              .swiper-slide-active{
                opacity: 1!important;
              }
            }
          }
        }
      }
    }
  }
}
@media(max-width: 820px){
  .team { 
    padding: 0px 20px 40px;
  }
}
@media(max-width: 520px){
  .team { 
    .team_cont { 
      .team_thumbs { 
        margin-right: 0px;
        .swiper { 
          .swiper-wrapper { 
            .swiper-slide { 
              padding: 0px!important;;
              height: 34px!important;
              width: 34px!important;
            }
          }
        }
      }
  
      .team_right { 
        h2 { 
          font-size: 30px;     
          margin-bottom: 20px;
        }
        .flex { 
          .teamSwiper { 
            .swiper-wrapper {   
              .swiper-slide { 
                
                .team_item { 
                  padding: 20px 16px;
                  .item_title { 
                    margin-bottom: 30px;
                    flex-direction: column;
                    align-items: flex-start;
                    img { 
                      width: 100px;
                      height: 100px;
                    }
                    .titletext { 
                      max-width: unset;
                      &::before{
                        display: none;
                      }
                      h3 { 
                        font-size: 24px;
                        line-height: 29px;
                      }
                      p { 
                        font-size: 18px;
                        line-height: 22px;
                      }
                    }
                  }
                  .infotext { 
                    font-size: 16px;
                    max-width: unset;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}