.token { 
  width: 100%;
  padding: 55px 20px 174px;
  background-image: url("../img/gradientbg4.png");
  background-repeat: no-repeat;
  background-position: top center;
	.token_cont { 
    width: 100%;
    max-width: 1180px;
    margin: 0 auto;
		h2 { 
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 300;
      font-size: 42px;
      line-height: 130%;
      color: #F3F3F3;
		}
		.token_spiral { 
      margin-top: 382px;
      margin: 382px auto 343px;
      width: 42%;
      height: 534px;
      position: relative;
			.central { 
        width: 100%;
        display: block;
        margin: auto;
        transition: all 0.5s;
        &:hover{
          transform: scale(1.1);
        }
			} 
			.token_item { 
        max-width: 282px;
        width: 100%;
        position: absolute;
        left: -70%;
        top: -9%;
        &:nth-child(3){
          left: -26%;
          top: -62%;
        }
        &:nth-child(4){
          left: 63%;
          top: -62%;
        }
        &:nth-child(5){
          left: unset;
          right: -70%;
          top: -9%;
        }
        &:nth-child(6){
          left: unset;
          right: -70%;
          top: 63%;
        }
        &:nth-child(8){
          left: -26%;
          bottom: -62%;
          top: unset;
        }
        &:nth-child(7){
          left: 63%;
          bottom: -62%;
          top: unset;
        }
        &:nth-child(9){
          left: -70%;
          top: 63%;
        }
				h4 { 
          background: #F3F3F3;
          border-radius: 20px;
          font-family: 'Montserrat';
          font-style: normal;
          font-weight: 700;
          font-size: 18px;
          line-height: 22px;
          color: #101010;          
          display: flex;
          justify-content: center;
          align-items: center;
          width: 100%;
          height: 82px;
          margin-bottom: 30px;
          transition: all 0.3s;
          &:hover{
            box-shadow: 0 0 15px #FFF;
          }
				}
				p { 
          max-width: 240px;
          width: 100%;
          margin: 0 auto;
          font-family: 'Montserrat';
          font-style: normal;
          font-weight: 400;
          font-size: 14px;
          line-height: 26px;
          text-align: center;
          color: #F3F3F3;
				}
			}
      .green{
        h4{
          background: #DDF247;
          &:hover{
            box-shadow: 0 0 15px #DDF247;
          }
        }
      }
		}
	}
}
@media(max-width: 960px){
  .token { 
    padding: 40px 20px 100px;
    background-image: unset;
    .token_cont { 
      h2 { 
        font-size: 34px;
        margin-bottom: 40px;
      }
      .token_spiral { 
        margin: 0;
        width: 100%;
        position: relative;
        height: auto;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 20px 10px;
        .central { 
          display: none;
        } 
        .token_item { 
          max-width: unset;
          width: auto;
          position: unset;
          left: 0!important;
          top: 0!important;
          right: 0!important;
          bottom: 0!important;
          h4 { 
            border-radius: 10px;
            font-size: 14px;
            line-height: 20px;
            height: 40px;
            margin-bottom: 10px;
          }
          p { 
            max-width: 240px;
            font-size: 12px;
            line-height: 16px;
          }
        }
      }
    }
  }
}
@media(max-width: 780px){
  .token { 
    padding: 40px 20px 40px;
    background-image: unset;
    .token_cont { 
      h2 { 
        font-size: 26px;
      }
    }
  }
  
}
@media(max-width: 500px){
  .token { 
    .token_cont { 
      h2 { 
        font-size: 24px;
        text-align: center;
      }
      .token_spiral { 
        margin: 0;
        width: 100%;
        position: relative;
        height: auto;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 20px 10px;
        .central { 
          display: none;
        } 
        .token_item { 
          h4 { 
            border-radius: 10px;
            font-size: 12px;
            line-height: 20px;
            height: 40px;
            margin-bottom: 10px;
          }
          p { 
            max-width: 240px;
            font-size: 12px;
            line-height: 14px;
          }
        }
      }
    }
  }
}