.everyday { 
  width: 100%;
  padding: 56px 20px 154px;
	.everyday_cont { 
    width: 100%;
    max-width: 1180px;
    margin: 0 auto;
		h2 { 
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 300;
      font-size: 42px;
      line-height: 130%;
      margin-bottom: 70px;
      color: #F3F3F3;
		}
		.everyday_flex { 
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
			.everyday_left { 
        max-width: 332px;
        width: 100%;
        margin-right: 15px;
				h4 { 
          font-family: 'Urbanist';
          font-style: normal;
          font-weight: 700;
          font-size: 30px;
          line-height: 45px;
          margin-bottom: 20px;
          letter-spacing: 0.3px;
          color: #F3F3F3;
				}
				p { 
          font-family: 'Urbanist';
          font-style: normal;
          font-weight: 400;
          font-size: 16px;
          line-height: 26px;
          letter-spacing: 0.01em;
          color: #F3F3F3;       
          margin-bottom: 50px;   
				}
				.btn { 
          width: 100%;
          display: flex;
          justify-content: space-between;
          align-items: center;
          max-width: 109px;
					button{
            width: 42px;
            height: 42px;
            background: #101010;
            box-shadow: 0px 6px 10px rgba(243, 243, 243, 0.06);
            border-radius: 50%;
            transition: all 0.3s;
            cursor: pointer;
            padding: 0;
            display: flex;
            justify-content: center;
            align-items: center;
            svg path{
              transition: all 0.3s;
            }
            &:hover{
              background: #F3F3F3;
              filter: unset;
              svg path{
                stroke: #101010!important;
              }
            }
          }
				}
			}

			.everydaySwiper { 
        width: 100%;
        max-width: 830px;
        padding: 0 24px;
        padding-bottom: 181px;
				.swiper-wrapper { 
					.swiper-slide { 
            width: 100%;
            max-width: 384px;
						.everyday_item { 
              width: 100%;
              position: relative;
							img { 
                width: 100%;
                height: 470px;
                border-radius: 30px;
							}

							.text { 
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
                bottom: -130px;
                padding: 20px;
                width: 100%;
                max-width: 350px;
                background: #101010;
                box-shadow: 0px 20px 60px rgba(243, 243, 243, 0.1);
                border-radius: 30px;
                transition: all 0.3s;
								h4 { 
                  font-family: 'Montserrat';
                  font-style: normal;
                  font-weight: 700;
                  font-size: 18px;
                  line-height: 152%;
                  color: #F3F3F3;
                  transition: all 0.3s;
                  margin-bottom: 20px;
									span { 
                    display: block;
									}
								}
                .flex_item { 
                  p { 
                    font-family: 'Montserrat';
                    font-style: normal;
                    transition: all 0.3s;
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 17px;
                    color: #F3F3F3;
                    opacity: 0.7;
                    margin-bottom: 10px;
                  }
                  span { 
                    font-family: 'Montserrat';
                    font-style: normal;
                    font-weight: 700;
                    font-size: 14px;
                    line-height: 17px;
                    color: #101010;
                    display: flex;
                    justify-content: flex-start;
                    align-items: center;               
                    svg{
                        margin-right: 5px;
                    }
                  }
                  .trans{
                    width: 100%;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    font-family: 'Montserrat';
                    font-style: normal;
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 17px;
                    color: #F3F3F3;        
                    span{
                      font-family: 'Montserrat';
                      font-style: normal;
                      font-weight: 700;
                      font-size: 14px;
                      line-height: 17px;
                      color: #F3F3F3;
                    }    
                    &:nth-child(2){
                      padding-bottom: 10px;
                      border-bottom: 1px solid #F3F3F3;
                    }        
                  }
                }
								.flex { 
                  width: 100%;
                  display: flex;
                  justify-content: space-between;
                  align-items: flex-start;
									gap: 15px;
                  flex-wrap: wrap;
								}
							}
						}
					}
          .swiper-slide-active{
            .everyday_item { 
              width: 100%;
              position: relative;
							img { 
                width: 100%;
                height: 470px;
                border-radius: 30px;
							}

							.text { 
                background: #DDF247;
                box-shadow: 0px 20px 60px rgba(221, 242, 71, 0.2);
								h4 { 
                  color: #101010;
								}
                .flex_item { 
                  p { 
                    color: #101010;
                  }
                  span { 
                    color: #101010;   
                  }
                  .trans{
                    color: #101010;        
                    span{
                      color: #101010;
                    }    
                    &:nth-child(2){
                      border-bottom: 1px solid #101010;
                    }        
                  }
                }
							}
						}
          }
				}
			}
		}
	}
}
@media(max-width: 960px){
  .everyday { 
    .everyday_cont { 
      h2 { 
        font-size: 34px;
      }
    }
  }
}
@media(max-width: 780px){
  .everyday { 
    padding: 40px 20px 0px;
    .everyday_cont { 
      h2 { 
        font-size: 26px;
        margin-bottom: 30px;
        text-align: center;
      }
      .everyday_flex { 
        flex-direction: column;
        align-items: center;
        .everyday_left { 
          max-width: 332px;
          margin-right: 0px;
          text-align: center;
          margin-bottom: 30px;
          h4 { 
            font-size: 20px;
            line-height: 26px;
            margin-bottom: 10px;
          }
          p { 
            font-size: 14px;
            line-height: 20px;     
            margin-bottom: 20px;   
          }
          .btn {
            max-width: 109px;
            margin: 0 auto;
            button{
              width: 42px;
              height: 42px;
            }
          }
        }
  
        .everydaySwiper { 
          max-width: 792px;
          padding-bottom: 110px;
          .swiper-wrapper { 
            .swiper-slide { 
              max-width: 280px;
              box-shadow: unset;
              .everyday_item { 
                box-shadow: unset;
                img { 
                  height: 300px;
                }
                .text { 
                  bottom: -100px;
                  padding: 16px;
                  box-shadow: unset;
                  max-width: 260px;
                  h4 { 
                    font-size: 16px;
                    margin-bottom: 10px;
                  }
                  .flex_item { 
                    p { 
                      font-size: 12px;
                      line-height: 14px;
                      margin-bottom: 5px;
                    }
                    span { 
                      font-size: 12px;
                      line-height: 14px;
                    }
                    .trans{
                      font-size: 12px;
                      line-height: 14px;     
                      span{
                        font-size: 12px;
                        line-height: 14px;
                      }       
                    }
                  }
                }
              }
            }
            .swiper-slide-active{
              .everyday_item { 
                img { 
                  width: 100%;
                  height: 300px;
                  border-radius: 30px;
                }
              }
            }
          }
        }
      }
    }
  }
}