.ammosinfo { 
  width: 100%;
  padding: 0 20px 230px;
	.ammosinfo_cont { 
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 90px 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    overflow: hidden;
    background: #DDF247;
    border-radius: 20px;
    &::before{
      content: url("../img/Ornament18.png");
      position: absolute;
      bottom: 74px;
      left: 60%;
      transform: translateX(-30%);
    }
    .bigspiral{
      max-width: 377px;
      width: 100%;
      height: 387px;
      position: absolute;
      left: -94px;
      top: -105px;
    }
    .bigspiral2{
      max-width: 464px;
      width: 100%;
      height: 476px;
      right: -200px;
      bottom: -90px;
      position: absolute;
    }
		.text { 
      max-width: 625px;
      width: 100%;
      z-index: 2;
      margin-right: 10px;
			h2 { 
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 700;
        font-size: 50px;
        line-height: 61px;
        color: #101010;    
        margin-bottom: 25px;    
			}
			p { 
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 26px;
        letter-spacing: 0.02em;
        color: #101010;
			}
		}

		a { 
      display: flex;
      justify-content: center;
      align-items: center;
      width: 222px;
      height: 70px;
      background: #101010;
      border-radius: 8px;
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 600;
      font-size: 20px;
      line-height: 26px;
      position: relative;
      z-index: 2;
      color: #F3F3F3;
		}
	}
}
@media(max-width: 800px){
  .ammosinfo { 
    padding: 0 20px 100px;
    .ammosinfo_cont { 
      padding: 40px 20px;
      flex-direction: column;
      align-items: flex-start;
      .bigspiral{
        max-width: 377px;
        height: 387px;
        left: -94px;
        top: -105px;
      }
      .bigspiral2{
        max-width: 464px;
        height: 476px;
        right: -200px;
        bottom: -90px;
      }
      .text { 
        max-width: 625px;
        margin-right: 0px;
        margin-bottom: 20px;
        h2 { 
          font-size: 30px;
          line-height: 36px;  
          margin-bottom: 10px;    
        }
        p { 
          font-size: 14px;
          line-height: 20px;
        }
      }
      a { 
        width: 222px;
        height: 70px;
        font-size: 20px;
        line-height: 26px;
      }
    }
  }
}
@media(max-width: 800px){
  .ammosinfo { 
    .ammosinfo_cont { 
      &::before{
        display: none;
      }
      .bigspiral{
        max-width: 220px;
        height: 230px;
        left: -94px;
        top: -105px;
      }
      .bigspiral2{
        max-width: 220px;
        height: 230px;
        right: -100px;
        bottom: -90px;
      }
    }
  }
}