.Tokenomic { 
  width: 100%;
  padding: 55px 20px 174px;
  position: relative;
  background-image: url("../img/gradientbg7.png");
  background-repeat: no-repeat;
  background-position: center top;
	.Tokenomic_cont { 
    width: 100%;
    max-width: 1180px;
    margin: 0 auto;
		h2 { 
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 300;
      font-size: 42px;
      line-height: 130%;
      color: #F3F3F3;
      margin-bottom: 70px;
		}
    .tokenssxemhover{
      transition: all 0.3s;
      &:hover{
        filter: drop-shadow(2px 4px 6px #FFFF);
      }
    }
		.spiral { 
      width: 100%;
      max-width: 879px;
      margin: 0 auto;
			svg { 
        width: 100%;
        height: auto;
			}
		}
	}
}
@media(max-width: 960px){
  .Tokenomic { 
    .Tokenomic_cont { 
      h2 { 
        font-size: 34px;
        margin-bottom: 40px;
      }
    }
  }
}
@media(max-width: 700px){
  .Tokenomic { 
    padding: 40px 20px 0px;
    background-image: unset;
    &::before{
      display: none;
    }
    .Tokenomic_cont { 
      h2 { 
        font-size: 28px;
        margin-bottom: 40px;
      }
    }
  }
}