* {
  box-sizing: border-box;
  outline: 0;
  margin: 0;
  padding: 0;
}
.cub{
  position: absolute;
  width: 103px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 9px;
  left: -48px;
  top: -38px;
  span{
    display: block;
    width: 2px;
    height: 2px;
    background: rgba(243, 243, 243, 0.3);
    transition: all 0.8s;
  }
  .active{
    background: #DDF247;
    box-shadow: 0 0 10px #DDF247;
  }
}
h2{
  position: relative;
}
.whatamoss { 
  padding: 100px 60px 174px;
  position: relative;
  background-image: url("../img/gradientbg2.png");
  background-repeat: no-repeat;
  background-position: left -59px;
	.whatamoss_cont { 
    width: 100%;
    max-width: 1180px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
		.whatamoss_left { 
      max-width: 585px;
      width: 100%;
      margin-right: 15px;
      .lasttext{
        width: 100%;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        margin-top: 66px;
        position: relative;
        color: #DDF247;
        svg{
          position: absolute;
          left: -26px;
          z-index: -1;
          top: -14px;
        }
      }
			h2 { 
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 300;
        font-size: 42px;
        line-height: 130%;
        color: #F3F3F3;
        margin-bottom: 50px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        img{
          margin-left: 15px;
        }
			}
			p { 
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 26px;
        color: #F3F3F3;
        margin-bottom: 10px;
			} 
			.flex { 
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        margin-top: 65px;
        gap: 10px;
				.item { 
          max-width: 255px;
          width: 100%;
					h4 { 
            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 700;
            font-size: 32px;
            line-height: 39px;
            color: #FFFFFF;
            margin: 0;
						
					}
          span { 
            color: #DDF247;
          }
					p { 
            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 26px;
            letter-spacing: 0.01em;
            color: #6F6F6F;     
            span{
              font-family: 'Montserrat';
              font-style: normal;
              font-weight: 700;
              font-size: 14px;
              line-height: 26px;
              /* or 186% */

              letter-spacing: 0.01em;

              color: #DDF247;
            }       
					}

					.imgflex { 
            width: 100%;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            flex-wrap: wrap;
            gap: 10px;
					}
				}
			}
		}

		.whatamoss_right { 
      max-width: 518px;
      width: 100%;
      .right_bg{
        background-image: url("../img/gradientbg3.png");
        background-repeat: no-repeat;
        background-position: right bottom;
        position: absolute;
        right: 100px;
        background-size: cover;
        bottom: 201px;
        width: 619.46px;
        height: 611.46px;
      }
			.right_img { 
        width: 100%;
        max-width: 471px;
        margin: 0 auto 55px;
        position: relative;
				svg { 
          width: 100%;
          height: auto;
				}
			}
      .text { 
        width: 100%;
        padding: 31px 33px 21px 36px;
        background: #222222;
        border-radius: 30px;
        position: relative;
        &::before{
          content: "“";
          position: absolute;
          left: 11px;
          top: -41px;
          font-family: 'Montserrat';
          font-style: normal;
          font-weight: 500;
          font-size: 114px;
          line-height: 139px;
          color: #FFFFFF;
        }
        p { 
          font-family: 'Montserrat';
          font-style: italic;
          font-weight: 300;
          font-size: 14px;
          line-height: 17px;
          color: #F3F3F3;
          opacity: 0.7;
          max-width: 410px;
          margin-bottom: 14px;
        }
        span { 
          font-family: 'Montserrat';
          font-style: normal;
          font-weight: 500;
          font-size: 14px;
          line-height: 17px;
          color: #F3F3F3;  
          display: block;
          text-align: right;          
        }
      }
		}
	}
}

