body{
  background-color: #101010;
  width: 100%;
  min-height: 100vh;
}
a,button{
  text-decoration: none;
  background: unset;
  border: unset;
  cursor: pointer;
  padding: 0;
  margin: 0;
}
.page{
  width: 100%;
  overflow: hidden;
  min-height: 100vh;
}
ul li{
  list-style: none;
}
.header {
  padding: 25px 16px;
  position: relative;
  z-index: 22;
  .header_bg{
    background-image: url("../img/gradientbg1.png");
    position: absolute;
    left: 0;
    top: 0;
    width: 400px;
    height: 400px;
    z-index: -1;
    background-size: contain;
    background-repeat: no-repeat;
  }
	.header_cont {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .header_left {
      max-width: 778px;
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-right: 10px;
      .openmenu{
        display: none;
        width: 30px;
        z-index: 2;
        position: relative;
        span{
          width: 100%;
          height: 2px;
          background: #fff;
          margin-bottom: 3px;
          display: block;
          &:last-child{
            margin-bottom: 0;
          }
        }
      }
			.logo {
        width: 100%;
        display: block;
        margin-right: 10px;
        max-width: 150px;
        z-index: 2;
        position: relative;
				img {
          width: 100%;
				}
			}

			.menu {
        width: 100%;
        max-width: 578px;
        display: flex;
        justify-content: space-between;
        align-items: center;
				li {
					a {
            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 26px;
            text-transform: uppercase;
            color: #F3F3F3;
            &:hover{
              color: #FFF;
              text-shadow: 0 10px 20px #FFF;
            }
					}
				}
			}
		}
		.header_right {
      max-width: 332px;
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
			.soc {
        max-width: 136px;
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
				a {
          background: rgba(243, 243, 243,0.1);
          border-radius: 7px;
          display: flex;
          justify-content: center;
          align-items: center;
          width: 32px;
          height: 32px;
          &:hover{
            box-shadow: 0 0 15px #FFF;
          }
				}
			}

			.white {
        background: #DDF247;
        border-radius: 8px;
        max-width: 176px;
        width: 100%;
        height: 50px;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 26px;
        color: #101010;
        display: flex;
        justify-content: center;
        align-items: center;
        &:hover{
          box-shadow: 0 0 15px #DDF247;
        }
			}
		}
    .header_mobile{
      position: absolute;
      left: 0;
      top: 0;
      width: 0%;
      transition: all 0.3s;
      overflow: hidden;
      height: 100vh;
      background: #fff;
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      flex-direction: column;
      padding: 20px 0;
      .clousmenu{
        width: 20px;
        position: absolute;
        right: 20px;
        top: 20px;
        span{
          width: 100%;
          height: 2px;
          background-color: #101010;
          display: block;
          position: relative;
          &:first-child{
            transform: rotate(45deg);
          }
          &:last-child{
            transform: rotate(-45deg);
            top: -2px;
          }
        }
      }
      .header_left {
        align-items: flex-start;
        flex-direction: column;
        padding: 0 20px;
        .openmenu{
          display: none;
        }
        .logo {
          width: 100%;
          display: block;
          margin-right: 10px;
          max-width: 150px;
          padding: 5px;
          background: rgba(0, 0, 0,.5);
          img {
            width: 100%;
          }
        }

        .menu {
          flex-direction: column;
          align-items: flex-start;
          padding: 0;
          display: flex;
          li {
            margin-bottom: 10px;
            a {
              font-size: 14px;
              line-height: 26px;
              color: #101010;
            }
          }
        }
      }
      .header_right {
        max-width: unset;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        padding: 0 20px;
        .soc {
          max-width: 136px;
          margin-bottom: 20px;
          a {
            width: 32px;
            height: 32px;
            background: #000;
          }
        }
        .white {
          max-width: 176px;
          height: 50px;
          font-size: 16px;
          line-height: 26px;
          display: flex;
        }
      }
    }
	}
}
@media(max-width: 964px){
  .header {
    .header_cont {
      z-index: 2;
      .header_left {
        max-width: unset;
        justify-content: space-between;
        max-width: 150px;
        .logo {
          width: 100%;
          display: block;
          margin-right: 10px;
          max-width: 150px;
          img {
            width: 100%;
          }
        }
        .menu {
          width: 100%;
          display: none;
        }
      }
      .header_right{

      }
    }
  }
}
@media(max-width: 540px){
  .header {
    .header_cont {
      .header_right{
        max-width: 136px;
        .white{
          display: none;

        }
      }
    }
  }
}
